import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs';
import { PaymasterService } from 'src/app/services/paymaster.service';

@Component({
  selector: 'app-paymaster',
  templateUrl: './paymaster.component.html',
  styleUrls: ['./paymaster.component.scss']
})
export class PaymasterComponent implements OnInit {
  items: any[];
  key: Observable<string>;

  constructor(public ngbActiveModal: NgbActiveModal, private paymasterService: PaymasterService) {}

  ngOnInit(): void {
    this.items = this.paymasterService.createItems(null);
    this.key = this.paymasterService.key;
  }

  onSelect(item: any) {
    this.paymasterService.select(item.key);
    this.ngbActiveModal.close();
  }
}
