import { HttpClient } from '@angular/common/http';
import { Base } from './base';
import { tap } from 'rxjs/operators';
import BigNumber from 'bignumber.js';

export class ReferralCodes extends Base {
  constructor(protected httpClient: HttpClient) {
    super(httpClient, 'referral_codes');
  }

  query(params?: any) {
    return this.api.query(params).pipe(
      tap((json) => json.referralCodes.forEach((record: any) => this.processRecord(record)))
    );
  }

  create(params: any) {
    return this.api.create(params);
  }

  private processRecord(record: any) {
    record.tradingVolume = new BigNumber(record.tradingVolume).fromDecimal();
    record.fee = new BigNumber(record.fee).fromDecimal();
    record.referrerRewardAmount = new BigNumber(record.referrerRewardAmount).fromDecimal();
    record.referrerPoints = new BigNumber(record.referrerPoints);
  }
}
