import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { EthWalletService } from 'src/app/services/eth-wallet.service';
import { ConnectButtonComponent } from 'src/app/shared/connect-button/connect-button.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-network',
  templateUrl: './network.component.html',
  styleUrls: ['./network.component.scss']
})
export class NetworkComponent implements OnInit {
  @Input() connectButton: ConnectButtonComponent;
  networks = environment.networks.filter((n) => n.contracts);

  constructor(public ngbActiveModal: NgbActiveModal,
    private ethWalletService: EthWalletService,
    ) { }

  ngOnInit(): void {
  }

  selectNetwork(network: any) {
    if (!network.params) {
      return;
    }
    localStorage.setItem('NETWORK', network.name);
    this.ethWalletService.selectNetwork(network);
    this.connectButton.changeWalletNetwork(network);
    this.ngbActiveModal.close();
  }

}
