import { Component, OnInit, ViewChild } from '@angular/core';
import { sharePublishReplay } from 'ngx-libs';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AccountService } from 'src/app/services/account.service';
import { EthWalletService, Wallet } from 'src/app/services/eth-wallet.service';
import { MainService } from 'src/app/services/main.service';
import { ConnectButtonComponent } from 'src/app/shared/connect-button/connect-button.component';
import { environment } from 'src/environments/environment';
import { PaymasterComponent } from '../modals/paymaster/paymaster.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { PaymasterService } from 'src/app/services/paymaster.service';
import { NetworkComponent } from '../modals/network/network.component';
import { XyComponent } from '../modals/xy/xy.component';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  features = environment.features;
  networks = environment.networks.filter((n) => n.contracts);
  currentNetwork: Observable<any>;
  supportedNetwork: Observable<boolean>;
  walletIcon: Observable<string>;
  announcements: Observable<any>;

  @ViewChild('connectButton') connectButton: ConnectButtonComponent;

  constructor(
    public accountService: AccountService,
    public paymasterService: PaymasterService,
    private ethWalletService: EthWalletService,
    private mainService: MainService,
    private ngbModal: NgbModal
  ) {
    const name = localStorage.getItem('NETWORK');
    const network = environment.networks.find((n) => n.name === name) || environment.networks[0];
    this.ethWalletService.selectNetwork(network);
    this.walletIcon = this.ethWalletService.wallet.pipe(
      map((wallet) => {
        switch (wallet) {
          case Wallet.Okx:
            return 'wallet_okxwallet.svg';
          default:
            return 'wallet_metamask_24.svg';
        }
      })
    );

    this.announcements = this.mainService.announcements.query().pipe(
      map((result) => result.announcements)
    );
  }

  ngOnInit(): void {
    this.currentNetwork = this.ethWalletService.selectedNetwork.pipe(map((network: any) => network));

    this.supportedNetwork = this.ethWalletService.chainId.pipe(
      map((chainId) => chainId === 0 || !!environment.networks.find((n) => n.params && parseInt(n.params.chainId) === chainId)),
      sharePublishReplay(1)
    );

    const wallet = parseInt(localStorage.getItem('WALLET'));
    if (wallet in Wallet) {
      const win: any = window;
      if (win.ethereum._metamask) {
        win.ethereum._metamask.isUnlocked().then((unlocked: boolean) => {
          if (unlocked) {
            this.ethWalletService.connect(wallet).subscribe();
          }
        })
      } else {
        this.ethWalletService.connect(wallet).subscribe();
      }
    }
  }

  selectNetwork(network: any) {
    if (!network.params) {
      return;
    }
    localStorage.setItem('NETWORK', network.name);
    this.ethWalletService.selectNetwork(network);
    this.connectButton.changeWalletNetwork(network);
  }

  disconnet() {
    if (localStorage.getItem('OBSERVE_ADDRESS')) {
      this.accountService.removeObserveAddress();
    } else {
      this.ethWalletService.disconnect();
    }
  }

  showPaymaster() {
    this.ngbModal.open(PaymasterComponent, {
      size: 'sm',
      centered: true
    });
  }

  showNetwork() {
    const ref = this.ngbModal.open(NetworkComponent, {
      size: 'sm',
      centered: true
    });
    (ref.componentInstance as NetworkComponent).connectButton = this.connectButton;
  }

  showXy() {
    this.ngbModal.open(XyComponent, {
      size: 'md',
      centered: true
    });
  }
}
