import { HttpClient } from '@angular/common/http';
import { Base } from './base';

export class Tasks extends Base {
  constructor(protected httpClient: HttpClient) {
    super(httpClient, 'tasks');
  }

  verify(id: number, body: any = {}) {
    return this.api.httpClient.post<any>(this.api.idPath(`${id}/verify`), body);
  }

  getStatus(ids: number[]) {
    return this.api.get('status', { id: ids.join() })
  }

  getFlash(referralCode: string) {
    return this.api.get('flash', { referralCode })
  }
}
