import { HttpClient } from '@angular/common/http';
import { Base } from './base';
import { tap } from 'rxjs/operators';
import BigNumber from 'bignumber.js';
import { camelCase } from 'src/app/rxjs';

export class RefereeRewards extends Base {
  constructor(protected httpClient: HttpClient) {
    super(httpClient, 'referee_rewards');
  }

  getClaimable() {
    return this.api.get('claimable').pipe(
      tap((json) => {
        json.claimable = new BigNumber(json.claimable).fromDecimal();
      })
    );
  }

  claim() {
    return this.api.httpClient.post<any>(this.api.idPath('claim'), {}).pipe(
      camelCase()
    );
  }
}
