import { HttpClient } from '@angular/common/http';
import { Base } from '../base';

export class Points extends Base {
  constructor(protected httpClient: HttpClient, private parent: string) {
    super(httpClient, `${parent}/points`);
  }

  query(params?: any) {
    return this.api.query(params);
  }

  checkIn(validateResult: any) {
    return this.api.httpClient.post(this.api.idPath('check_in'), validateResult);
  }
}
