import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpService } from 'ngx-libs-http';
import { EMPTY, Observable, throwError } from 'rxjs';
import { ReferralCodes } from '../api/auth/referral-codes';
import { Account } from '../api/auth/account';
import { EthWalletService } from './eth-wallet.service';
import { catchError, mergeMap, share, skip, take, tap } from 'rxjs/operators';
import { camelCase } from '../rxjs';
import { MessageService } from './message.service';
import { AccountService } from './account.service';
import { ReferrerRewards } from '../api/auth/referrer-rewards';
import { RefereeRewards } from '../api/auth/referee-rewards';
import { Tasks } from '../api/auth/tasks';

const MESSAGE = 'Signing this message proves to Zomma you are in control of your account without giving Zomma access to any sensitive information. Message ID: %{nonce}';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  httpClient: HttpClient;
  unauthorized: Observable<any>;
  currentManager: Observable<any>;
  referralCodes: ReferralCodes;
  referrerRewards: ReferrerRewards;
  refereeRewards: RefereeRewards;
  account: Account;
  tasks: Tasks;
  lastSign: Observable<any>;
  lastMessage = 0;

  constructor(
    private httpService: HttpService,
    private ethWalletService: EthWalletService,
    private messageService: MessageService,
    private accountService: AccountService
  ) {
    this.httpClient = this.httpService.get('auth');
    this.unauthorized = this.httpService.unauthorized('auth');
    this.referralCodes = new ReferralCodes(this.httpClient);
    this.referrerRewards = new ReferrerRewards(this.httpClient);
    this.refereeRewards = new RefereeRewards(this.httpClient);
    this.account = new Account(this.httpClient);
    this.tasks = new Tasks(this.httpClient);

    this.accountService.account.pipe(
      skip(1)
    ).subscribe((a) => {
      if (a !== localStorage.getItem('ADDRESS')) {
        localStorage.removeItem('ACCESS_TOKEN');
        localStorage.removeItem('SAVED_REFERRAL_CODE');
        localStorage.removeItem('ADDRESS');
      }
    });
  }

  withSignedin<T>(func: () => Observable<T>): Observable<T> {
    return func().pipe(
      catchError((e) => {
        if (e.status === 401) {
          return this.signIn().pipe(
            catchError((e) => {
              const now = Date.now();
              if (now - this.lastMessage > 1000) {
                this.lastMessage = now;
                this.messageService.warning('Sign in failed');
              }
              return EMPTY;
            }),
            mergeMap((r) => {
              localStorage.setItem('ACCESS_TOKEN', r.accessToken);
              return func();
            })
          );
        } else {
          return throwError(e);
        }
      })
    );
  }

  private signIn() {
    if (this.lastSign) {
      return this.lastSign;
    }
    return this.accountService.account.pipe(
      take(1),
      mergeMap((account) => {
        if (!account) {
          return throwError(new Error('No account'));
        }
        const nonce = Math.floor(Date.now() / 1000);
        const message = MESSAGE.replace('%{nonce}', nonce.toString());
        this.lastSign = this.ethWalletService.sign({ message, address: account, encoding: 'utf8' }).pipe(
          mergeMap((signature) => this.account.signIn({ signature, nonce, address: account })),
          camelCase(),
          tap(() => {
            this.lastSign = null;
            localStorage.setItem('ADDRESS', account);
          }),
          catchError((e) => {
            this.lastSign = null;
            return throwError(e);
          }),
          share()
        );
        return this.lastSign;
      })
    );
  }
}
